<template>
  <header>
    <div class="container">
      <div class="logo">
        <router-link to="/">
          <img src="../assets/images/logo.png" />
        </router-link>
      </div>
      <!--gnb에 클릭 이벤트 생성, isActive 메서드 호출-->
      <ul class="gnb">
        <li class="on" @click="isActive">
          <router-link to="/"> HOME </router-link>
        </li>
        <li @click="isActive">
          <router-link to="/aboutview"> ABOUT </router-link>
        </li>
        <li @click="isActive">
          <router-link to="/contactview"> CONTACT </router-link>
        </li>
        <li @click="isActive">
          <router-link to="/loginkakao"> LOGIN </router-link>
        </li>
      </ul>
    </div>
  </header>
</template>

<script>
export default {
  methods: {
    isActive(event) {
      const targetEl = event.target.parentNode;
      console.log(targetEl);
      //.gnb > li요소들 모두선택
      const gnbMenus = document.querySelectorAll(".gnb>li");
      //.gnb>li의 갯수만큼 반복
      for (let i = 0; i < gnbMenus.length; i++) {
        //.gnb>li의 순번이 클릭한 요소의 부모요소와 같으면 on클래스를 추가하고 그렇지 않으면 on클래스를 제거
        if (gnbMenus[i] === targetEl) {
          gnbMenus[i].className = "on"; //해당요소(li)에 클래스를 on으로 설정
        } else {
          gnbMenus[i].className = ""; //그 외 li요소에는 클래스를 빈문자열로 설정(클래스제거)
        }
      }
    },
  },
};
</script>

<style scoped>
header {
  min-width: 1300px;
  background-color: white;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.2);
  position: relative;
  z-index: 10;
}
header > .container {
  padding: 0 100px;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.gnb {
  display: flex;
}
.gnb > li {
  margin-left: 32px;
  font-size: 14px;
}
.gnb > li > a {
  color: #333;
}
.gnb > li.on > a {
  color: #10c9c3;
}
</style>