<template>
  <main>
    <section id="banner">
      <h1>UI/UX &amp; Graphic Designer</h1>
      <p>
        I am a Graphic &amp; Web Designer based in ansan, specializing <br />
        in User InterFace Design and Development
      </p>
    </section>
    <section id="contents">
      <div class="container">
        <!--this.items에 담겨있는 데이터를 v-for를 이용하여 순차적으로 요소를 추가하여 넣어준다 이때 이미지의 src값을 데이터의 path값으로 할당하여 이미지를 표시해준다-->
        <!--box를 클릭하면 modalView라는 메서드가 호출됨 이때 item정보를 인수로 전달-->

        <article
          class="box"
          v-for="(item, i) in this.items"
          :key="i"
          @click="modalView(item)"
        >
          <img :src="item.path" :alt="item.name" />
          <div class="eye"></div>
        </article>
      </div>
    </section>
    <!--v-if로 modalRender가 true일때 modalView가 나타남-->
    <!--자식 컴포넌트에서 close-modal이벤트가 호출되면 closeModal 메서드 호출-->
    <!--자식 컴포넌트로 item데이터를 전달-->
    <modal-view v-if="modalRender" @close-modal="closeModal" :item="item"/>
  </main>
</template>

<script>
import ModalView from "./ModalView.vue";
export default {
  components: { ModalView },
  data() {
    return {
      //items배열에 상품에 관련된 데이터를 담아줌
      //assets폴더에 담겨진 이미지를 v-bind로 src경로를 설정할때는 require라는 메서드로 연결해야합니다
      items: [
        {
          name: "상품1",
          price: 10000,
          //path :"이미지경로" = 단순한 String값이기 때문에 require를 사용하여 해당 경로를 불러오듯 담아주어야한다
          path: require("../assets/images/image01.jpg"),
        },
        {
          name: "상품2",
          price: 14000,
          path: require("../assets/images/image02.jpg"),
        },
        {
          name: "상품3",
          price: 20000,
          path: require("../assets/images/image03.jpg"),
        },
        {
          name: "상품4",
          price: 25000,
          path: require("../assets/images/image04.jpg"),
        },
        {
          name: "상품5",
          price: 16000,
          path: require("../assets/images/image05.jpg"),
        },
        {
          name: "상품6",
          price: 130000,
          path: require("../assets/images/image06.jpg"),
        },
        {
          name: "상품7",
          price: 100000,
          path: require("../assets/images/image07.jpg"),
        },
        {
          name: "상품8",
          price: 60000,
          path: require("../assets/images/image08.jpg"),
        },
        {
          name: "상품9",
          price: 110000,
          path: require("../assets/images/image09.jpg"),
        },
        {
          name: "상품10",
          price: 13200,
          path: require("../assets/images/image10.jpg"),
        },
        {
          name: "상품11",
          price: 10230,
          path: require("../assets/images/image11.jpg"),
        },
        {
          name: "상품12",
          price: 180000,
          path: require("../assets/images/image12.jpg"),
        },
      ],
      //클릭한 item데이터를 객체로 담을 변수 생성
      item: {},
      //modalRender가 true면 modal이 나타남\
      modalRender: false,
    };
  },
  methods: {
    modalView(item) {
      //modalRender를 true로 변경하여 modal을 화면에표시
      this.modalRender = true;
      //item정보를 this.item에 할당
      this.item = item;
    },
    closeModal() {
      //modalRender를 false로 변경하여 modal을 화면에서 제거
      this.modalRender = false;
    },
  },
};
</script>

<style scoped>
/*배너*/
#banner {
  min-width: 1300px;
  height: 320px;
  background-color: #f6f6f6;
  padding-top: 100px;
  box-sizing: border-box;
  text-align: center;
  color: #191919;
}
#banner > h1 {
  font-size: 36px;
  margin-bottom: 20px;
}
#banner > p {
  line-height: 1.4;
  color: #666;
}
/*콘텐츠영역*/
#contents > .container {
  display: flex;
  flex-wrap: wrap;
}
#contents .box {
  width: 325px;
  height: 280px;
  position: relative;
  overflow: hidden;
}
.box > .eye {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  /*아이콘*/
  /*../ = 상위폴더로 이동*/
  background: url(../assets/images/eye.png) no-repeat center
    rgba(16, 201, 195, 0.6);
  opacity: 0;
  transform: scale(5);
  transition: 0.5s;
}
/*hover효과*/
.box:hover > .eye {
  opacity: 1;
  transform: scale(1);
  cursor: pointer;
}
</style>